import Vue from 'vue'
import Router from 'vue-router'
import CenterCity from './views/city/CenterCity'
import TwoCity from './views/city/TwoCity'
import PetInfo from './views/PetInfo'
import UserInfo from './views/UserInfo'
import Explore from './views/Explore'

Vue.use(Router)

export default new Router({
    routes: [
        {path: '/', component: CenterCity, meta: {title: ''}},
        {path: '/twoCity', component: TwoCity, meta: {title: ''}},
        {path: '/petInfo', component: PetInfo, meta: {title: ''}},
        {path: '/userInfo', component: UserInfo, meta: {title: ''}},
        {path: '/explore', component: Explore, meta: {title: ''}},
    ]
})
